// pages/my/my.js
Page({

  /**
   * 
   */
  data: {
    activeNames: [],
    array: [],
    code: '',
    complaintsCounts: 0,
    receiveCounts: 0,
    grade: 0
  },
  sendInfo(){
    wx.navigateTo({
      url: '../sendInfo/sendInfo',
    })
  },
  receiveInfo(){
    wx.navigateTo({
      url: '../receiveInfo/receiveInfo',
    })
  },
  userInfo(){
    wx.navigateTo({
      url: '../rule/rule',
    })
  },

  //
  onChange(event) {
    this.setData({
      activeNames: event.detail,
    });
    var app = getApp();
    var that = this;
    //
    wx.request({
      url: 'http://express.applinzi.com/gradetwo.php', 
      method:'POST',
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      data: {
        Sno: app.globalData.user.username,
        Grade: that.data.grade
      },
      success(res){}
    })
  },

  /**
   * --
   */
  onLoad: function (options) {
    //
    var app = getApp();
    var that = this;
    wx.request({
      url: 'http://express.applinzi.com/isVolunteer.php', 
      method:'POST',
      data: {
          userSno: app.globalData.user.username,
      },
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      success (res) {
        that.setData({
         code: res.data.code
       })
      }
    })
  },

  /**
   * --
   */
  onReady: function () {
    var that = this;
    var app = getApp();
    //
    wx.request({
      url: 'http://express.applinzi.com/grade.php', 
      method:'POST',
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      data: {
        Sno: app.globalData.user.username,
      },
      success (res) { 
        if(res.data.code === 0){          
          that.setData({
            complaintsCounts : res.data.complaintsCounts,
            receiveCounts : res.data.receiveCounts,
          })
        }
        var rc = that.data.receiveCounts;
        var cc = that.data.complaintsCounts;
        var grade = '';
        if(rc/6){
          grade = (Math.floor(rc/6))*0.2;
        }   
        if(cc/3){
          grade -= (Math.floor(cc/3))*0.1;
        }    
        that.setData({
          grade : grade
        })
      },
    })
  },

  /**
   * --
   */
  onShow: function () {

  },

  /**
   * --
   */
  onHide: function () {

  },

  /**
   * --
   */
  onUnload: function () {

  },

  /**
   * --
   */
  onPullDownRefresh: function () {
    var that = this;
    var app = getApp();
    //
    wx.request({
      url: 'http://express.applinzi.com/grade.php', 
      method:'POST',
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      data: {
        Sno: app.globalData.user.username,
      },
      success (res) { 
        if(res.data.code === 0){          
          that.setData({
            complaintsCounts : res.data.complaintsCounts,
            receiveCounts : res.data.receiveCounts,
          })
        }
        var rc = that.data.receiveCounts;
        var cc = that.data.complaintsCounts;
        var grade = '';
        if(rc/6){
          grade = (Math.floor(rc/6))*0.2;
        }   
        if(cc/3){
          grade -= (Math.floor(cc/3))*0.1;
        }    
        that.setData({
          grade : grade
        })
      },
      complete: function (res) {
        wx.hideNavigationBarLoading();
        wx.stopPullDownRefresh();
      }
    })
  },

  /**
   * 
   */
  onReachBottom: function () {

  },

  /**
   * 
   */
  onShareAppMessage: function () {

  },
})