// pages/login/login.js
Page({

  /**
   * 
   */
  data: {
    username: '',
    password: '',
    isPassword: true
  },

  username:function(e){
    this.setData({
      username:e.detail.value
    });
  },
  password:function(e){
    this.setData({
      password:e.detail.value
    });
  },
  changePwd(){
    this.setData({
      isPassword: !this.data.isPassword
    })
  },

  loginBtn(){
    var that = this;
    wx.request({
      url: 'https://onepanda.justyunmeng.com/user/studentLogin', 
      method:'POST',
      data: {
          username: that.data.username,
          password: that.data.password
        },
      header: {
        'content-type': 'application/json' 
      },
      success (res) {
        if(res.data.code === 400){
          wx.showModal({
            title:'',
            content:'',
            showCancel:false,
            success(res){}
          })
        }
        if(res.data.code === 200){
          getApp().globalData.user = res.data.data;
          wx.reLaunch({
            url: '../send/send',
          })
        }
      }
    })
  },

  /**
   * --
   */
  onLoad: function (options) {
  
  },

  /**
   * --
   */
  onReady: function () {

  },

  /**
   * --
   */
  onShow: function () {

  },

  /**
   * --
   */
  onHide: function () {

  },

  /**
   * --
   */
  onUnload: function () {

  },

  /**
   * --
   */
  onPullDownRefresh: function () {

  },

  /**
   * 
   */
  onReachBottom: function () {

  },

  /**
   * 
   */
  onShareAppMessage: function () {

  }
})