import Dialog from '../../miniprogram_npm/@vant/weapp/dialog/dialog';

Page({
  /**
   * 
   */
  data: {
    Ono : '',
    ReceiveAddress : '',
    Sname : '',
    Sphone : '',
    SendAddress : '',
    Remark : ''
  },
  signUp: function(event) {
    Dialog.alert({
      title: '',
      message: '1121022808',
    });
  },
  OnoChange(event){
    this.setData({
      Ono: event.detail
    });
  },
  ReceiveAddressChange(event){
    this.setData({
      ReceiveAddress: event.detail
    });
  },
  SnameChange(event){
    this.setData({
      Sname: event.detail
    });
  },
  SphoneChange(event){
    this.setData({
      Sphone: event.detail
    });
  },
  SendAddressChange(event){
    this.setData({
      SendAddress: event.detail
    });
  },
  RemarkChange(event){
    this.setData({
      Remark: event.detail
    });
  },

  //
  sendBtn:function(e){
    var that = this ;
    var app = getApp();
    if(that.data.Ono == ''){
      wx.showModal({
        title:'',
        content:'',
        showCancel:false,
        success(res){}
      })
    }else if(that.data.SendAddress == ''){
      wx.showModal({
        title:'',
        content:'',
        showCancel:false,
        success(res){}
      })
    }else if(that.data.Sphone == ''){
      wx.showModal({
        title:'',
        content:'',
        showCancel:false,
        success(res){}
      })
    }else if(that.data.ReceiveAddress == ''){
      wx.showModal({
        title:'',
        content:'',
        showCancel:false,
        success(res){}
      })
    }else if(that.data.Sphone.length != 11){
      wx.showModal({
        title:'',
        content:'11',
        showCancel:false,
        success(res){}
      })
    }else{
      wx.request({
        url: 'http://express.applinzi.com/send.php', 
        method:'POST',
        data: {
            Ono : that.data.Ono,
            ReceiveAddress : that.data.ReceiveAddress,
            Sname : that.data.Sname,
            Sphone : that.data.Sphone,
            SendAddress : that.data.SendAddress,
            Remark : that.data.Remark,
            sendSno : app.globalData.user.username,
        },
        header: {
          'content-type': 'application/x-www-form-urlencoded' 
        },
        success (res) {
          if(res.data.code === 1){
            wx.showModal({
              title:'',
              content:res.data.msg,
              showCancel:false
            })
          }else if(res.data.code === 2){
            wx.showModal({
              title:'',
              content:'',
              showCancel:false
            })
          }else if(res.data.code === -1){
            wx.showModal({
              title:'',
              content:'',
              showCancel:false
            })
          }else if(res.data.code === 0){
            wx.showModal({
              title:'',
              content:'-',
              showCancel:false
            })
          }
        },
        fail(res){
          wx.showModal({
            title:'',
            content:'',
            showCancel:false
          })
        }
      })
    }    
  },
  /**
   * --
   */
  onLoad: function (options) {

  },

  /**
   * --
   */
  onReady: function () {

  },

  /**
   * --
   */
  onShow: function () {

  },

  /**
   * --
   */
  onHide: function () {

  },

  /**
   * --
   */
  onUnload: function () {

  },

  /**
   * --
   */
  onPullDownRefresh: function () {

  },

  /**
   * 
   */
  onReachBottom: function () {

  },

  /**
   * 
   */
  onShareAppMessage: function () {

  }
})
