// pages/sendInfo/sendInfo.js

Page({
  /**
   * 
   */
  data: {
    tip: '',
    buttonDisabled: false,
    modalHidden: true,
    show: false,

    activeName: '1',
    array: [],
    tt:'',
    oo:'',
    ooo:'',
    complaints:''
  },

  //  
  statusChange(e){
    var that = this;
    var o = e.target.dataset.ono;
    this.setData({
         ooo: o
    })
   
    //
    wx.request({
      url: 'http://express.applinzi.com/updateGoodsStatus.php', 
      method:'POST',
      data: {
          Ono : that.data.ooo,
      },
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      success (res) {
        if(res.data.code === 0){
          that.setData({
            array : res.data.data
          })
          wx.showModal({
            title : '',
            content : '',
            showCancel : false
          })
        }else if(res.data.code === 2){
         wx.showModal({
            title : '',
            content : "''",
            showCancel : false
          })
        }
      },
      fail(res){
        wx.showModal({
          title: '',
          content: '',
          showCancel: false
        })
      }
    })
  },
  complaints:function(e){
    this.setData({
      complaints:e.detail.value
    });
  },

  showModal:function(e){   
    var o = e.target.dataset.ono;
    var t = e.target.dataset.telphone;
    this.setData({
      tt:t,
      oo:o,
      modalHidden:!this.data.modalHidden
    })
   },

   modalBindaconfirm:function(){ 
    var that = this;
    this.setData({
     modalHidden:!this.data.modalHidden,
     show:!this.data.show,
     buttonDisabled:!this.data.buttonDisabled,
   })
   // 
   wx.request({
     url: 'http://express.applinzi.com/insertComplaints.php', 
     method:'POST',
     data: {
         Ono : that.data.oo,
         Phone : that.data.telVolunteer,
         Complaints : that.data.complaints,
     },
     header: {
       'content-type': 'application/x-www-form-urlencoded' 
     },
     success (res) {
      if(res.data.code === 0){
       wx.showModal({
         title:'',
         content:'',
         showCancel:false,
         success(res){}
       })
      }else if(res.data.code === 2){
       wx.showModal({
         title:'',
         content:'',
         showCancel:false,
         success(res){}
       })
      }
     },
     fail(res){
       wx.showModal({
         title:'',
         content:'',
         showCancel:false
       })
     }
   })
  },
   modalBindcancel:function(){
     this.setData({
     modalHidden:!this.data.modalHidden,
     tip:''
    })
   },
  onChange(event) {
    this.setData({
      activeName: event.detail,
    });
  },
 
  /**
   * --
   */
  onLoad: function (options) {
    //
    var that = this;
    var app = getApp();
    wx.request({
      url: 'http://express.applinzi.com/sendInfo.php', 
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      method:'POST',
      data: {
          sendSno : app.globalData.user.username,
      },
      success (res) {  
        if(res.data.error_code === 0){
            that.setData({
              array : res.data.data
            })
        }else if(res.data.error_code === 2){
          wx.showModal({
            title:'',
            content:'',
            showCancel:false,
            success(res){}
          })
        }
      }
    })
  },

  /**
   * --
   */
  onReady: function () {

  },

  /**
   * --
   */
  onShow: function () {

  },

  /**
   * --
   */
  onHide: function () {

  },

  /**
   * --
   */
  onUnload: function () {
    wx.navigateBack({
      //delta: 1
      url:('../my/my')
    })
  },

  /**
   * --
   */
  onPullDownRefresh: function () {
    var that = this;
    var app = getApp();
    wx.request({
      url: 'http://express.applinzi.com/sendInfo.php', 
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      method:'POST',
      data: {
          sendSno : app.globalData.user.username,
      },
      success (res) {  
        if(res.data.error_code === 0){
            that.setData({
              array : res.data.data
            })
        }else if(res.data.error_code === 2){
          wx.showModal({
            title:'',
            content:'',
            showCancel:false,
            success(res){}
          })
        }
      },
      complete: function (res) {
        wx.hideNavigationBarLoading(); 
        wx.stopPullDownRefresh();
      }
    })
  },

  /**
   * 
   */
  onReachBottom: function () {

  },

  /**
   * 
   */
  onShareAppMessage: function () {

  }
})