// pages/receiveInfo/receiveInfo.js
Page({
  /**
   * 
   */
  data: {
    activeName: '1',
    array:[],
    oo:''
  },
  
  //  
  statusChange(e){
    var that = this;
    var o = e.target.dataset.ono;
    this.setData({
         oo: o
    })
   
    //
    wx.request({
      url: 'http://express.applinzi.com/updateStatus.php', 
      method:'POST',
      data: {
          Ono : that.data.oo,
      },
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      success (res) {
        if(res.data.code === 0){
          that.setData({
            array : res.data.data
          })
          wx.showModal({
            title:'',
            content:'',
            showCancel:false
          })
        }else if(res.data.code === 2){
         wx.showModal({
            title:'',
            content:'',
            showCancel:false
          })
        }
      },
      fail(res){
        wx.showModal({
          title:'',
          content:'',
          showCancel:false
        })
      }
    })
  },

  onChange(event) {
    this.setData({
      activeName: event.detail,
    });
  },

  /**
   * --
   * 
   */
  onLoad: function (options) {
    var that = this;
    var app = getApp();
    wx.request({
      url: 'http://express.applinzi.com/receiveInfo.php', 
      method:'POST',
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      data: {
        Sno: app.globalData.user.username,
      },
      success (res) {
        if(res.data.error_code === 0){          
            that.setData({
              array : res.data.data
            })
        }else if(res.data.error_code === 2){ 
          wx.showModal({
            title:'',
            content:'',
            showCancel:false,
            success(res){}
          })
        }
      }
    })
  },

  /**
   * --
   */
  onReady: function () {

  },

  /**
   * --
   */
  onShow: function () {

  },

  /**
   * --
   */
  onHide: function () {

  },

  /**
   * --
   */
  onUnload: function () {

  },

  /**
   * --
   */
  onPullDownRefresh: function () {
    var that = this;
    var app = getApp();
    wx.request({
      url: 'http://express.applinzi.com/receiveInfo.php', 
      method:'POST',
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      data: {
        Sno: app.globalData.user.username,
      },
      success (res) {
        if(res.data.error_code === 0){          
            that.setData({
              array : res.data.data
            })
        }else if(res.data.error_code === 2){ 
          wx.showModal({
            title:'',
            content:'',
            showCancel:false,
            success(res){}
          })
        }
      },
      complete: function (res) {
        wx.hideNavigationBarLoading();
        wx.stopPullDownRefresh();
      }
    })
  },

  /**
   * 
   */
  onReachBottom: function () {

  },

  /**
   * 
   */
  onShareAppMessage: function () {

  }
})