// pages/receive/receive.js
Page({
  /**
   * 
   */
  data: {
    currentTab: 0,
    clientHeight: 0,
    activeName: '1',
    //
    arraya:[],
    arrayb: [],
    arrayc: [],
    //
    codeImagea: '',
    codeImageb: '',
    codeImagec: '',

    buttonDisabled: false,
    modalHidden: true,
    show: false,
    telVolunteer: '',
    oo: '',
    code: ''
  },
  
  telVolunteer:function(e){
    this.setData({
      telVolunteer:e.detail.value
    });
  },

  // 
 showModal:function(e){
     var o = e.target.dataset.ono;
    this.setData({
     modalHidden:!this.data.modalHidden,
     oo: o
    })
   },
   modalBindaconfirm:function(){ 
     var that = this;
     this.setData({
     modalHidden:!this.data.modalHidden,
     show:!this.data.show,
     buttonDisabled:!this.data.buttonDisabled,
    })

    // 
    var app = getApp();
    wx.request({
      url: 'http://express.applinzi.com/insertPhone.php', 
      method:'POST',
      data: {
          Ono : that.data.oo,
          Phone : that.data.telVolunteer,
          Sno : app.globalData.user.username,
      },
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      success (res) {
      //
       if(res.data[0].code === 1){
         //
        if(res.data[1].code === 3){
          wx.showModal({
            title:'',
            content:'-',
            showCancel:false,
            success(res){}
          })
        }else if(res.data[1].code === -3){
          wx.showModal({
            title:'',
            content:'',
            showCancel:false,
            success(res){}
          })
         }
       }else if(res.data[0].code === -1){
        wx.showModal({
          title:'',
          content:'',
          showCancel:false,
          success(res){}
        })
       }
      },
      fail(res){
        wx.showModal({
          title:'',
          content:'',
          showCancel:false
        })
      }
    })

    //
    wx.request({
      url: 'http://express.applinzi.com/insertPhonetwo.php', 
      method:'POST',
      data: {
          Ono : that.data.oo,
      },
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      success (res) {
        if(res.data.code === -2){
          wx.showModal({
            title:'',
            content:'',
            showCancel:false,
            success(res){}
          })
        }
      },
      fail(res){
        wx.showModal({
          title:'',
          content:'',
          showCancel:false
        })
      }
    })
   },
   modalBindcancel:function(){
     this.setData({
     modalHidden:!this.data.modalHidden,
    })
   },
  /**
   * --
   */
  onLoad: function (options) {
    var that = this
    wx.getSystemInfo({
      success: function (res) {
        that.setData({
          clientHeight: res.windowHeight 
        });
      }
    })
    //
    var app = getApp();
    var that=this
    wx.request({
      url: 'http://express.applinzi.com/isVolunteer.php', 
      method:'POST',
      data: {
          userSno: app.globalData.user.username,
      },
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      success (res) {
       that.setData({
         code: res.data.code
       })
      }
    })
  },

  /**
   * --
   */
  onReady: function () {

  },

  /**
   * --
   */
  onShow: function () {
    //
    var that = this;
    wx.request({
      url: 'http://express.applinzi.com/receive.php', 
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      success (res) {   
        //
        if(res.data[0].error_code === 1){          
            that.setData({
              arraya : res.data[0].data,
              codeImagea : res.data[0].error_code
            })
        }else if(res.data[0].error_code === 2){
          that.setData({
            codeImagea : res.data[0].error_code
          })
        }
        //
        if(res.data[1].error_code === 3){          
          that.setData({
            arrayb : res.data[1].data,
            codeImageb : res.data[1].error_code
          })
        }else if(res.data[1].error_code === 4){
          that.setData({
            codeImageb : res.data[1].error_code
          })
        }
        //
        if(res.data[2].error_code === 5){          
          that.setData({
            arrayc : res.data[2].data,
            codeImagec : res.data[2].error_code
          })
        }else if(res.data[2].error_code === 6){
          that.setData({
            codeImagec : res.data[2].error_code
          })
        }
      }
    })
  },

  /**
   * --
   */
  onHide: function () {

  },

  /**
   * --
   */
  onUnload: function () {

  },

  /**
   * --
   */
  onPullDownRefresh: function () {
    var that = this;
    wx.request({
      url: 'http://express.applinzi.com/receive.php', 
      header: {
        'content-type': 'application/x-www-form-urlencoded' 
      },
      success (res) {   
        if(res.data[0].error_code === 1){          
            that.setData({
              arraya : res.data[0].data,
              codeImagea : res.data[0].error_code
            })
        }else if(res.data[0].error_code === 2){
          that.setData({
            codeImagea : res.data[0].error_code
          })
        }
        if(res.data[1].error_code === 3){          
          that.setData({
            arrayb : res.data[1].data,
            codeImageb : res.data[1].error_code
          })
        }else if(res.data[1].error_code === 4){
          that.setData({
            codeImageb : res.data[1].error_code
          })
        }
        if(res.data[2].error_code === 5){          
          that.setData({
            arrayc : res.data[2].data,
            codeImagec : res.data[2].error_code
          })
        }else if(res.data[2].error_code === 6){
          that.setData({
            codeImagec : res.data[2].error_code
          })
        }
      },
      complete: function (res) {
        wx.hideNavigationBarLoading(); 
        wx.stopPullDownRefresh();
      }
    })
  },

  /**
   * 
   */
  onReachBottom: function () {

  },

  /**
   * 
   */
  onShareAppMessage: function () {

  },
  //
  swiperTab: function (e) {
    var that = this;
    that.setData({
      currentTab: e.detail.current
    });
  },
  //
  clickTab: function (e) {
    var that = this;
    if (that.data.currentTab === e.target.dataset.current) {
      return false;
    } else {
      that.setData({
        currentTab: e.target.dataset.current
      })
    }
  } ,
  onChange(event) {
    this.setData({
      activeName: event.detail,
    });
  },
  onOpen(event) {
    Toast(`: ${event.detail}`);
  },
  onClose(event) {
    Toast(`: ${event.detail}`);
  },
})
